import type { GradeRecord } from '@/types';
import { extractNumeric, extractBobotValue } from './sqlHelpers';
import type { SqlNullInt32 } from './sqlHelpers';

// ============================================================================
// Constants
// ============================================================================

/** Default grade component weights (percentage) - used as fallback */
export const DEFAULT_GRADE_WEIGHTS = {
  aktivitas_partisipatif: 15,
  hasil_proyek: 35,
  quiz: 5,
  tugas: 5,
  uts: 15,
  uas: 25,
} as const;

export const BOBOT_FIELDS = [
  'bobot_partisipatif',
  'bobot_proyek',
  'bobot_kuis',
  'bobot_tugas',
  'bobot_uts',
  'bobot_uas',
] as const;

/** Fields to compare when checking for grade changes */
export const GRADE_COMPARE_FIELDS: (keyof GradeRecord)[] = [
  'aktivitas_partisipatif',
  'hasil_proyek',
  'quiz',
  'tugas',
  'uts',
  'uas',
  'nilai_angka',
  'nilai_huruf',
  'nilai_indeks',
];

// ============================================================================
// Grade Component Configuration
// ============================================================================

/** Get grade components with dynamic weights from class data */
type GradeWeightSource = Partial<{
  bobot_partisipatif: unknown;
  bobot_proyek: unknown;
  bobot_kuis: unknown;
  bobot_tugas: unknown;
  bobot_uts: unknown;
  bobot_uas: unknown;
}> | null;

export const getGradeComponents = (classData?: GradeWeightSource) => {
  const partisipatif = extractBobotValue(classData?.bobot_partisipatif);
  const proyek = extractBobotValue(classData?.bobot_proyek);
  const kuis = extractBobotValue(classData?.bobot_kuis);
  const tugas = extractBobotValue(classData?.bobot_tugas);
  const uts = extractBobotValue(classData?.bobot_uts);
  const uas = extractBobotValue(classData?.bobot_uas);

  return [
    { key: 'aktivitas_partisipatif', weight: partisipatif ?? DEFAULT_GRADE_WEIGHTS.aktivitas_partisipatif },
    { key: 'hasil_proyek', weight: proyek ?? DEFAULT_GRADE_WEIGHTS.hasil_proyek },
    { key: 'quiz', weight: kuis ?? DEFAULT_GRADE_WEIGHTS.quiz },
    { key: 'tugas', weight: tugas ?? DEFAULT_GRADE_WEIGHTS.tugas },
    { key: 'uts', weight: uts ?? DEFAULT_GRADE_WEIGHTS.uts },
    { key: 'uas', weight: uas ?? DEFAULT_GRADE_WEIGHTS.uas },
  ] as const;
};

// ============================================================================
// Grade Calculations
// ============================================================================

/** Calculate letter grade and index from numeric value */
export const calculateLetterGrade = (nilaiAngka: number | null): { huruf: string; indeks: number } => {
  if (nilaiAngka === null || nilaiAngka === undefined) return { huruf: '', indeks: 0 };
  if (nilaiAngka >= 80) return { huruf: 'A', indeks: 4.0 };
  if (nilaiAngka >= 75) return { huruf: 'AB', indeks: 3.5 };
  if (nilaiAngka >= 70) return { huruf: 'B', indeks: 3.0 };
  if (nilaiAngka >= 65) return { huruf: 'BC', indeks: 2.5 };
  if (nilaiAngka >= 60) return { huruf: 'C', indeks: 2.0 };
  if (nilaiAngka >= 55) return { huruf: 'CD', indeks: 1.5 };
  if (nilaiAngka >= 40) return { huruf: 'D', indeks: 1.0 };
  return { huruf: 'E', indeks: 0.0 };
};

/** Calculate numeric grade from components based on weights (handles SqlNullFloat64) */
export const calculateNumericGrade = (
  components: Partial<GradeRecord>,
  gradeComponents?: ReturnType<typeof getGradeComponents>
): number | null => {
  let totalWeight = 0;
  let totalValue = 0;
  let hasAnyValue = false;

  const componentsToUse = gradeComponents ?? getGradeComponents();
  componentsToUse.forEach(({ key, weight }) => {
    const rawValue = components[key as keyof GradeRecord];
    const value = extractNumeric(rawValue);
    if (value !== null) {
      totalValue += value * weight;
      totalWeight += weight;
      hasAnyValue = true;
    }
  });

  if (!hasAnyValue || totalWeight === 0) return null;
  return parseFloat((totalValue / totalWeight).toFixed(2));
};

/** Generate a unique grade key for a student */
export const generateGradeKey = (grade: GradeRecord, studentKrsId: number | null, studentNim: string): string => {
  if (grade.id && grade.id > 0) {
    return `id:${grade.id}`;
  }
  if (grade.krs_id) {
    const krsId = typeof grade.krs_id === 'object' ? (grade.krs_id as SqlNullInt32).Int32 : grade.krs_id;
    return `krs:${krsId}`;
  }
  if (studentKrsId) {
    return `krs:${studentKrsId}`;
  }
  return `nim:${studentNim}`;
};
