import { useState, useRef, useCallback, useMemo } from 'react';
import { useQueryClient } from '@tanstack/react-query';
import { useCreateAttendance, useUpdateAttendance } from '../../../hooks/useAttendances';
import { extractString, normalizeStatus, ATTENDANCE_STATUSES } from '../utils';
import type { SqlNullString } from '../utils';
import type { AttendanceRecord } from '../../../types';
import { apiService } from '../../../services/api';

interface UseAttendanceManagerProps {
  classId: string | undefined;
  meetingIds: number[];
  attendanceMap: Record<number, AttendanceRecord[]>;
  isEditing: boolean;
}

export const useAttendanceManager = ({
  classId,
  meetingIds,
  attendanceMap,
  isEditing,
}: UseAttendanceManagerProps) => {
  const queryClient = useQueryClient();
  const justSavedAttendanceRef = useRef(false);

  // State
  const [savingCells, setSavingCells] = useState<Record<string, boolean>>({});
  const [initialAttendanceSnapshot, setInitialAttendanceSnapshot] = useState<Record<string, string>>({});
  const [modifiedCells, setModifiedCells] = useState<Set<string>>(new Set());
  const [pendingStatusChanges, setPendingStatusChanges] = useState<Record<string, string>>({});
  const [originalAttendanceCache, setOriginalAttendanceCache] = useState<Record<number, AttendanceRecord[]>>({});
  const [activeCell, setActiveCell] = useState<string | null>(null);
  const [currentShortcutStatus, setCurrentShortcutStatus] = useState<string | null>(null);

  // Mutations
  const createAttendanceMutation = useCreateAttendance();
  const updateAttendanceMutation = useUpdateAttendance();

  // Build initial snapshot
  const buildInitialSnapshot = useCallback(() => {
    if (justSavedAttendanceRef.current) {
      return;
    }

    const snap: Record<string, string> = {};
    Object.values(attendanceMap).forEach((records) => {
      records.forEach((r) => {
        const status = extractString(r.status_kehadiran as string | SqlNullString | undefined | null);
        snap[`${r.nim}-${r.id_pertemuan}`] = normalizeStatus(status, ATTENDANCE_STATUSES);
      });
    });
    setInitialAttendanceSnapshot(snap);
    if (!isEditing) {
      setOriginalAttendanceCache(attendanceMap);
    }
  }, [attendanceMap, isEditing]);

  // Recalculate modified cells
  const recalcModified = useCallback(() => {
    const next = new Set<string>();
    Object.entries(pendingStatusChanges).forEach(([key, val]) => {
      const original = initialAttendanceSnapshot[key] ?? '';
      if (normalizeStatus(original, ATTENDANCE_STATUSES) !== normalizeStatus(val, ATTENDANCE_STATUSES)) {
        next.add(key);
      }
    });
    setModifiedCells(next);
  }, [pendingStatusChanges, initialAttendanceSnapshot]);

  // Update single cell
  const updateSingleCell = useCallback(
    (key: string, newStatus: string) => {
      const [nim, meetingIdStr] = key.split('-');
      const meetingId = Number(meetingIdStr);
      const queryKey = ['class-attendances', classId, meetingIds];

      queryClient.setQueryData(queryKey, (old: typeof attendanceMap) => {
        const clone = { ...(old || {}) };
        const arr = [...(clone[meetingId] || [])];
        const existingIdx = arr.findIndex((r) => r.nim === nim);

        if (existingIdx >= 0) {
          arr[existingIdx] = { ...arr[existingIdx], status_kehadiran: newStatus };
        } else {
          arr.push({
            id: -Date.now(),
            id_pertemuan: meetingId,
            nim,
            status_kehadiran: newStatus,
          } as AttendanceRecord);
        }

        clone[meetingId] = arr;
        return clone;
      });

      setPendingStatusChanges((prev) => ({ ...prev, [key]: newStatus }));
      recalcModified();
    },
    [classId, meetingIds, queryClient, recalcModified]
  );

  // Commit attendance changes
  const commitAttendanceChanges = useCallback(
    async (skipExit = false) => {
      if (!isEditing) return;

      const changes = Object.entries(pendingStatusChanges);
      if (!changes.length) {
        return;
      }

      setSavingCells((prev) => {
        const next = { ...prev };
        changes.forEach(([key]) => (next[key] = true));
        return next;
      });

      try {
        await Promise.all(
          changes.map(async ([key, status]) => {
            const [nim, meetingIdStr] = key.split('-');
            const meetingId = Number(meetingIdStr);
            const existing = attendanceMap[meetingId]?.find((r) => r.nim === nim);

            if (existing && existing.id > 0) {
              await updateAttendanceMutation.mutateAsync({
                id: existing.id,
                data: { status_kehadiran: status || undefined },
              });
            } else {
              await createAttendanceMutation.mutateAsync({
                id_pertemuan: meetingId,
                nim,
                status_kehadiran: status || undefined,
              });
            }
          })
        );

        // Manually refetch fresh data from the server
        const map: Record<number, AttendanceRecord[]> = {};
        await Promise.all(
          meetingIds.map(async (meetingId) => {
            const records = await apiService.getMeetingAttendances(meetingId);
            map[meetingId] = records;
          })
        );

        // Update the query cache with fresh data
        queryClient.setQueryData(['class-attendances', classId, meetingIds], map);

        // Update the original cache
        setOriginalAttendanceCache(map);

        // Mark that we just saved successfully
        justSavedAttendanceRef.current = true;

        if (!skipExit) {
          setPendingStatusChanges({});
          setModifiedCells(new Set());
          setActiveCell(null);
        }
      } finally {
        setSavingCells({});
      }
    },
    [
      isEditing,
      pendingStatusChanges,
      attendanceMap,
      meetingIds,
      updateAttendanceMutation,
      createAttendanceMutation,
      queryClient,
      classId,
    ]
  );

  // Enter edit mode
  const enterEditMode = useCallback(() => {
    setOriginalAttendanceCache(attendanceMap);
    setPendingStatusChanges({});
    setModifiedCells(new Set());
    setActiveCell(null);
    setCurrentShortcutStatus(null);
  }, [attendanceMap]);

  // Exit edit mode
  const exitEditMode = useCallback(() => {
    const queryKey = ['class-attendances', classId, meetingIds];

    // Don't restore old data if we just saved successfully
    if (justSavedAttendanceRef.current) {
      justSavedAttendanceRef.current = false;
    } else {
      queryClient.setQueryData(queryKey, originalAttendanceCache);
    }

    setPendingStatusChanges({});
    setModifiedCells(new Set());
    setActiveCell(null);
    setCurrentShortcutStatus(null);
  }, [classId, meetingIds, queryClient, originalAttendanceCache]);

  // Check if there are unsaved changes
  const hasUnsavedChanges = useMemo(() => {
    return modifiedCells.size > 0;
  }, [modifiedCells]);

  return {
    // State
    savingCells,
    initialAttendanceSnapshot,
    modifiedCells,
    pendingStatusChanges,
    activeCell,
    currentShortcutStatus,
    hasUnsavedChanges,

    // State setters
    setActiveCell,
    setCurrentShortcutStatus,
    setPendingStatusChanges,

    // Methods
    buildInitialSnapshot,
    updateSingleCell,
    commitAttendanceChanges,
    enterEditMode,
    exitEditMode,
    recalcModified,
  };
};
