import { extractTimeHHMM } from './sqlHelpers';
import type { SqlNullString, SqlNullTime } from './sqlHelpers';

// ============================================================================
// Date & Time Formatters
// ============================================================================

/** Format date for display with day of week */
export const formatDate = (dateValue: string | SqlNullTime): string => {
  let dateString = '';
  if (typeof dateValue === 'object' && dateValue !== null) {
    if (!dateValue.Valid) return '-';
    dateString = dateValue.Time;
  } else {
    dateString = dateValue;
  }
  if (!dateString) return '-';
  const date = new Date(dateString);
  const dayOfWeek = date.toLocaleDateString('en-US', { weekday: 'short' });
  const formattedDate = date.toLocaleDateString('en-US', { day: '2-digit', month: 'short', year: 'numeric' });
  return `${dayOfWeek}, ${formattedDate}`;
};

/** Format date for compact display (for table headers) */
export const formatDateCompact = (dateValue: string | SqlNullTime): string => {
  let dateString = '';
  if (typeof dateValue === 'object' && dateValue !== null) {
    if (!dateValue.Valid) return '-';
    dateString = dateValue.Time;
  } else {
    dateString = dateValue;
  }
  if (!dateString) return '-';
  const date = new Date(dateString);
  const dayOfWeek = date.toLocaleDateString('en-US', { weekday: 'short' });
  const day = date.toLocaleDateString('en-US', { day: '2-digit' });
  const month = date.toLocaleDateString('en-US', { month: 'short' });
  return `${dayOfWeek}\n${day} ${month}`;
};

/** Format time for display */
export const formatTime = (timeValue: string | SqlNullString | SqlNullTime | null | undefined): string => {
  const raw = extractTimeHHMM(timeValue);
  return raw || '-';
};
