// ============================================================================
// SQL Null Type Definitions
// ============================================================================

export type SqlNullString = { String: string; Valid: boolean };
export type SqlNullTime = { Time: string; Valid: boolean };
export type SqlNullFloat64 = { Float64: number; Valid: boolean };
export type SqlNullInt32 = { Int32: number; Valid: boolean };

// ============================================================================
// SQL Value Extraction Helpers
// ============================================================================

/** Extract string from SqlNullString or plain string */
export const extractString = (val: string | SqlNullString | undefined | null): string => {
  if (!val) return '';
  if (typeof val === 'string') return val;
  return val.Valid ? val.String : '';
};

/** Extract date string from SqlNullTime or plain string, returns ISO date YYYY-MM-DD */
export const extractDateISO = (val: string | SqlNullTime | undefined | null): string => {
  if (!val) return '';
  let raw = '';
  if (typeof val === 'string') {
    raw = val;
  } else if (val.Valid) {
    raw = val.Time;
  }
  if (!raw) return '';
  const d = new Date(raw);
  return isNaN(d.getTime()) ? '' : d.toISOString().slice(0, 10);
};

/** Extract time string, returns HH:MM format */
export const extractTimeHHMM = (val: string | SqlNullString | SqlNullTime | undefined | null): string => {
  if (!val) return '';
  let raw = '';
  if (typeof val === 'string') {
    raw = val;
  } else if ('String' in val && val.Valid) {
    raw = val.String;
  } else if ('Time' in val && val.Valid) {
    raw = val.Time;
  }
  if (!raw) return '';
  const parts = raw.split(':');
  return parts.length >= 2 ? `${parts[0].padStart(2, '0')}:${parts[1].padStart(2, '0')}` : raw;
};

/** Extract numeric value from various SQL null types (properly checks Valid property) */
export const extractNumeric = (val: unknown): number | null => {
  if (val === null || val === undefined) return null;
  if (typeof val === 'number') return val;
  if (typeof val === 'object' && val !== null) {
    if ('Float64' in val && (val as SqlNullFloat64).Valid) return (val as SqlNullFloat64).Float64;
    if ('Int32' in val && (val as SqlNullInt32).Valid) return (val as SqlNullInt32).Int32;
  }
  return null;
};

/** Extract primitive value from SQL null types */
export const extractPrimitive = (val: unknown): string | number | boolean | null => {
  if (val === null || val === undefined) return null;
  if (typeof val === 'number' || typeof val === 'string' || typeof val === 'boolean') return val;
  if (typeof val === 'object' && val !== null) {
    if ('Float64' in val && (val as SqlNullFloat64).Valid) return (val as SqlNullFloat64).Float64;
    if ('Int32' in val) return (val as SqlNullInt32).Int32;
    if ('String' in val && (val as SqlNullString).Valid) return (val as SqlNullString).String;
  }
  return null;
};

/** Helper to extract numeric value from SqlNullFloat64 or number */
export const extractBobotValue = (val: unknown): number | null => {
  if (val === null || val === undefined) return null;
  if (typeof val === 'number') return val;
  if (typeof val === 'object' && val !== null && 'Float64' in val && 'Valid' in val) {
    const sqlNull = val as { Float64: number; Valid: boolean };
    return sqlNull.Valid ? sqlNull.Float64 : null;
  }
  return null;
};

/** Get KRS ID from student object (handles multiple field name variants) */
type StudentKrsLike = {
  id_krs?: string | number | null;
  krs_id?: string | number | null;
  krsId?: string | number | null;
  krs?: {
    id?: string | number | null;
    KRS?: {
      id?: string | number | null;
    } | null;
  } | null;
} | null | undefined;

export const getStudentKrsId = (student: StudentKrsLike): number | null => {
  const val =
    student?.id_krs ??
    student?.krs_id ??
    student?.krsId ??
    student?.krs?.id ??
    student?.krs?.KRS?.id;
  return val !== undefined && val !== null ? Number(val) : null;
};

/** Normalize attendance status to canonical form */
export const normalizeStatus = (val: string, validStatuses: readonly string[]): string => {
  const found = validStatuses.find((s) => s.toLowerCase() === val.toLowerCase());
  return found ?? val;
};
