import { useQuery, useMutation, useQueryClient } from '@tanstack/react-query';
import { apiService } from '../services/api';
import type { GetGradesParams, GradeRecord, CreateGradeRequest, UpdateGradeRequest } from '../types';

export function useGrades(params?: GetGradesParams) {
  return useQuery<GradeRecord[], Error>({
    queryKey: ['grades', params?.krs_id ?? null, params?.kelas_id ?? null],
    queryFn: () => apiService.getGrades(params),
    enabled: Boolean(params && (params.krs_id !== undefined || params.kelas_id !== undefined)),
  });
}

export function useCreateGrade(options?: { skipInvalidation?: boolean }) {
  const qc = useQueryClient();
  return useMutation({
    mutationFn: (data: CreateGradeRequest) => apiService.createGrade(data),
    onSuccess: async () => {
      if (!options?.skipInvalidation) {
        // For create, we need to invalidate since we don't have the new ID yet
        qc.invalidateQueries({ queryKey: ['grades'] });
      }
    },
  });
}

export function useUpdateGrade(options?: { skipInvalidation?: boolean }) {
  const qc = useQueryClient();
  return useMutation({
    mutationFn: ({ id, data }: { id: number; data: UpdateGradeRequest }) => apiService.updateGrade(id, data),
    onSuccess: async (_response, variables) => {
      if (!options?.skipInvalidation) {
        // Fetch the updated grade and update cache instead of invalidating all
        try {
          const updatedGrade = await apiService.getGrade(variables.id);

          // Update all matching grade queries
          qc.setQueriesData<GradeRecord[]>(
            { queryKey: ['grades'], exact: false },
            (old) => {
              if (!old) return old;
              return old.map(grade =>
                grade.id === variables.id ? updatedGrade : grade
              );
            }
          );
        } catch (error) {
          // Fallback to invalidation if fetch fails
          qc.invalidateQueries({ queryKey: ['grades'] });
        }
      }
    },
  });
}

export function useDeleteGrade() {
  const qc = useQueryClient();
  return useMutation({
    mutationFn: (id: number) => apiService.deleteGrade(id),
    onSuccess: () => {
      qc.invalidateQueries({ queryKey: ['grades'] });
    },
  });
}
