import { useState, useCallback, useMemo } from 'react';
import { useQueryClient } from '@tanstack/react-query';
import { useUpdateMeeting } from '../../../hooks/useAttendances';
import { extractString, extractDateISO, extractTimeHHMM } from '../utils';
import type { SqlNullString } from '../utils';
import type { MeetingSchedule } from '../../../types';

interface UseMeetingManagerProps {
  classIdNum: number;
  meetings: MeetingSchedule[];
}

export const useMeetingManager = ({ classIdNum, meetings }: UseMeetingManagerProps) => {
  const queryClient = useQueryClient();

  // State
  const [editedTopics, setEditedTopics] = useState<Record<number, string>>({});
  const [editedDates, setEditedDates] = useState<Record<number, string>>({});
  const [editedTimes, setEditedTimes] = useState<Record<number, string>>({});
  const [editedLecturers, setEditedLecturers] = useState<Record<number, string>>({});
  const [editedAbsensiAktif, setEditedAbsensiAktif] = useState<Record<number, boolean>>({});

  // Mutations
  const updateMeetingMutation = useUpdateMeeting();

  // Start editing a topic
  const startEditTopic = useCallback(
    (meetingId: number, currentTopik: string, currentDate: string, currentTime: string, currentLecturer: string, currentAbsensiAktif: boolean) => {
      setEditedTopics((prev) => ({ ...prev, [meetingId]: currentTopik || '' }));
      setEditedDates((prev) => ({ ...prev, [meetingId]: currentDate || '' }));
      setEditedTimes((prev) => ({ ...prev, [meetingId]: currentTime || '' }));
      setEditedLecturers((prev) => ({ ...prev, [meetingId]: currentLecturer || '' }));
      setEditedAbsensiAktif((prev) => ({ ...prev, [meetingId]: currentAbsensiAktif }));
    },
    []
  );

  // Cancel editing a topic
  const cancelEditTopic = useCallback((meetingId: number) => {
    setEditedTopics((prev) => {
      const next = { ...prev };
      delete next[meetingId];
      return next;
    });
    setEditedDates((prev) => {
      const next = { ...prev };
      delete next[meetingId];
      return next;
    });
    setEditedTimes((prev) => {
      const next = { ...prev };
      delete next[meetingId];
      return next;
    });
    setEditedLecturers((prev) => {
      const next = { ...prev };
      delete next[meetingId];
      return next;
    });
    setEditedAbsensiAktif((prev) => {
      const next = { ...prev };
      delete next[meetingId];
      return next;
    });
  }, []);

  // Save a topic
  const saveTopic = useCallback(
    async (meetingId: number) => {
      const newTopic = editedTopics[meetingId] ?? '';
      const newDateRaw = editedDates[meetingId] ?? '';
      const newTimeRaw = editedTimes[meetingId] ?? '';
      const newLecturer = editedLecturers[meetingId] ?? '';
      const newAbsensiAktif = editedAbsensiAktif[meetingId] ?? false;

      // Convert to expected formats
      const newDateISO = newDateRaw ? `${newDateRaw}T00:00:00.000Z` : undefined;
      const newTimeWithSeconds = newTimeRaw ? `${newTimeRaw}:00` : undefined;

        await updateMeetingMutation.mutateAsync({
          id: meetingId,
          data: {
            topik: newTopic || undefined,
            tgl_pertemuan: newDateISO,
            jam_pertemuan: newTimeWithSeconds,
            id_dosen: newLecturer || undefined,
            absensi_aktif: newAbsensiAktif,
          },
        });

        // Optimistically update cache
        queryClient.setQueryData<MeetingSchedule[]>(['class-meetings', classIdNum], (prev) => {
          if (!prev) return prev;
          return prev.map((m) =>
            m.id === meetingId
              ? {
                  ...m,
                  topik: newTopic,
                  tgl_pertemuan: newDateISO || m.tgl_pertemuan,
                  jam_pertemuan: newTimeWithSeconds || m.jam_pertemuan,
                  id_dosen: newLecturer || m.id_dosen,
                  absensi_aktif: newAbsensiAktif,
                }
              : m
          );
        });

        cancelEditTopic(meetingId);
    },
    [editedTopics, editedDates, editedTimes, editedLecturers, editedAbsensiAktif, updateMeetingMutation, queryClient, classIdNum, cancelEditTopic]
  );

  // Enter edit mode
  const enterEditMode = useCallback(() => {
    const topics: Record<number, string> = {};
    const dates: Record<number, string> = {};
    const times: Record<number, string> = {};
    const lecturers: Record<number, string> = {};
    const absensiAktif: Record<number, boolean> = {};
    meetings.forEach((meeting) => {
      topics[meeting.id] = extractString(meeting.topik as string | SqlNullString);
      dates[meeting.id] = extractDateISO(meeting.tgl_pertemuan);
      times[meeting.id] = extractTimeHHMM(meeting.jam_pertemuan as string | SqlNullString);
      lecturers[meeting.id] = extractString(meeting.id_dosen as string | SqlNullString);
      // Extract absensi_aktif (boolean or SqlNullBool)
      let isAbsensiAktif = false;
      if (meeting.absensi_aktif !== null && meeting.absensi_aktif !== undefined) {
        if (typeof meeting.absensi_aktif === 'boolean') {
          isAbsensiAktif = meeting.absensi_aktif;
        } else if (typeof meeting.absensi_aktif === 'object' && 'Bool' in meeting.absensi_aktif) {
          isAbsensiAktif = meeting.absensi_aktif.Valid ? meeting.absensi_aktif.Bool : false;
        }
      }
      absensiAktif[meeting.id] = isAbsensiAktif;
    });
    setEditedTopics(topics);
    setEditedDates(dates);
    setEditedTimes(times);
    setEditedLecturers(lecturers);
    setEditedAbsensiAktif(absensiAktif);
  }, [meetings]);

  // Exit edit mode
  const exitEditMode = useCallback(() => {
    setEditedTopics({});
    setEditedDates({});
    setEditedTimes({});
    setEditedLecturers({});
    setEditedAbsensiAktif({});
  }, []);

  // Check if there are unsaved changes
  const hasUnsavedChanges = useMemo(() => {
    for (const meeting of meetings) {
      const id = meeting.id;

      // Topic
      const editedTopik = editedTopics[id] ?? '';
      const currentTopik = extractString(meeting.topik as string | SqlNullString);
      if (editedTopik !== currentTopik) return true;

      // Date
      const editedDate = editedDates[id] ?? '';
      const currentDateISO = extractDateISO(meeting.tgl_pertemuan);
      if (editedDate !== currentDateISO) return true;

      // Time
      const editedTime = editedTimes[id] ?? '';
      const currentTime = extractTimeHHMM(meeting.jam_pertemuan as string | SqlNullString);
      if (editedTime !== currentTime) return true;

      // Lecturer
      const editedLecturer = editedLecturers[id] ?? '';
      const currentLecturer = extractString(meeting.id_dosen as string | SqlNullString);
      if (editedLecturer !== currentLecturer) return true;

      // Absensi Aktif
      const editedAbsensi = editedAbsensiAktif[id] ?? false;
      let currentAbsensi = false;
      if (meeting.absensi_aktif !== null && meeting.absensi_aktif !== undefined) {
        if (typeof meeting.absensi_aktif === 'boolean') {
          currentAbsensi = meeting.absensi_aktif;
        } else if (typeof meeting.absensi_aktif === 'object' && 'Bool' in meeting.absensi_aktif) {
          currentAbsensi = meeting.absensi_aktif.Valid ? meeting.absensi_aktif.Bool : false;
        }
      }
      if (editedAbsensi !== currentAbsensi) return true;
    }
    return false;
  }, [meetings, editedTopics, editedDates, editedTimes, editedLecturers, editedAbsensiAktif]);

  return {
    // State
    editedTopics,
    editedDates,
    editedTimes,
    editedLecturers,
    editedAbsensiAktif,
    hasUnsavedChanges,

    // State setters
    setEditedTopics,
    setEditedDates,
    setEditedTimes,
    setEditedLecturers,
    setEditedAbsensiAktif,

    // Methods
    startEditTopic,
    cancelEditTopic,
    saveTopic,
    enterEditMode,
    exitEditMode,
  };
};
