import type { MeetingSchedule, GradeRecord } from '../../../types';
import { extractString, formatDate, formatTime } from '../utils';
import type { SqlNullString } from '../utils';

// ============================================================================
// Report Generator Service
// ============================================================================

type StudentSummary = {
  nim: string;
  nama: string;
  [key: string]: unknown;
};

interface ReportData {
  className: string;
  courseName: string;
  courseCode: string;
  totalSks: number;
  lecturerNames: string[];
  students: StudentSummary[];
  sortedMeetings: MeetingSchedule[];
  attendanceLookup: Record<string, Record<number, string>>;
  gradeRecords: GradeRecord[];
  gradeWeights: {
    aktivitas_partisipatif: number;
    hasil_proyek: number;
    quiz: number;
    tugas: number;
    uts: number;
    uas: number;
  };
  lecturerLookup: Record<string, string>;
  findGradeForStudent: (student: { nim: string }) => GradeRecord;
}

/**
 * Generate a PDF report of the class summary
 */
export const generatePDFReport = async (contentRef: HTMLDivElement) => {
  try {
    const [{ default: html2canvas }, { default: jsPDF }] = await Promise.all([
      import('html2canvas-pro'),
      import('jspdf'),
    ]);

    const canvas = await html2canvas(contentRef, {
      scale: 1.5,
      useCORS: true,
      logging: false,
      backgroundColor: '#ffffff',
    });

    const imgWidth = 210;
    const pageHeight = 297;
    const imgHeight = (canvas.height * imgWidth) / canvas.width;
    let heightLeft = imgHeight;
    const pdf = new jsPDF('p', 'mm', 'a4');
    let position = 0;
    const imgData = canvas.toDataURL('image/jpeg', 0.85);

    pdf.addImage(imgData, 'JPEG', 0, position, imgWidth, imgHeight);
    heightLeft -= pageHeight;

    while (heightLeft > 0) {
      position = heightLeft - imgHeight;
      pdf.addPage();
      pdf.addImage(imgData, 'JPEG', 0, position, imgWidth, imgHeight);
      heightLeft -= pageHeight;
    }

    return pdf;
  } catch (error) {
    throw new Error('Failed to generate PDF. Please try again.');
  }
};

/**
 * Generate an XLSX report of the class summary
 */
export const generateXLSXReport = async (data: ReportData) => {
  try {
    const XLSX = await import('xlsx');
    const workbook = XLSX.utils.book_new();

    // Sheet 1: Class Description
    const classDescData = [
      ['Class Summary Report'],
      [],
      ['Course Code', data.courseCode],
      ['Course Name', data.courseName],
      ['Class', data.className],
      ['Total SKS', data.totalSks],
      ['Lecturers', data.lecturerNames.join(', ')],
      ['Total Students', data.students.length],
      ['Total Meetings', data.sortedMeetings.length],
      ['Generated Date', new Date().toLocaleDateString('id-ID')],
    ];
    const classDescSheet = XLSX.utils.aoa_to_sheet(classDescData);
    classDescSheet['!cols'] = [{ wch: 20 }, { wch: 40 }];
    XLSX.utils.book_append_sheet(workbook, classDescSheet, 'Class Description');

    // Sheet 2: Attendance Overview
    const attendanceHeaders = ['NIM', 'Name', ...data.sortedMeetings.map((m) => `P${m.pertemuan_ke}`)];
    const attendanceData = [attendanceHeaders];

    data.students.forEach((student) => {
      const studentAttendance = data.attendanceLookup[student.nim] || {};
      const row = [student.nim, student.nama];

      data.sortedMeetings.forEach((meeting) => {
        const status = studentAttendance[meeting.id] || '';
        row.push(status || '-');
      });

      attendanceData.push(row);
    });

    const attendanceSheet = XLSX.utils.aoa_to_sheet(attendanceData);
    attendanceSheet['!cols'] = [{ wch: 15 }, { wch: 25 }, ...data.sortedMeetings.map(() => ({ wch: 12 }))];
    XLSX.utils.book_append_sheet(workbook, attendanceSheet, 'Attendance Overview');

    // Sheet 3: Meeting Summary
    const meetingSummaryData = [
      ['Meeting', 'Date', 'Time', 'Lecturers', 'Topic'],
      ...data.sortedMeetings.map((meeting) => {
        const hasValidDate =
          meeting.tgl_pertemuan &&
          (typeof meeting.tgl_pertemuan === 'string' ||
            (typeof meeting.tgl_pertemuan === 'object' &&
              'Valid' in meeting.tgl_pertemuan &&
              meeting.tgl_pertemuan.Valid));

        let topik = '';
        if (meeting.topik) {
          if (typeof meeting.topik === 'string') {
            topik = meeting.topik;
          } else if ('String' in meeting.topik && meeting.topik.Valid) {
            topik = meeting.topik.String;
          }
        }

        const lecturerId = extractString(meeting.id_dosen as string | SqlNullString);
        const lecturerName = lecturerId && data.lecturerLookup[lecturerId] ? data.lecturerLookup[lecturerId] : '-';

        return [
          `P${meeting.pertemuan_ke}`,
          hasValidDate ? formatDate(meeting.tgl_pertemuan) : '-',
          formatTime(meeting.jam_pertemuan),
          lecturerName,
          topik || '-',
        ];
      }),
    ];

    const meetingSheet = XLSX.utils.aoa_to_sheet(meetingSummaryData);
    meetingSheet['!cols'] = [{ wch: 15 }, { wch: 20 }, { wch: 12 }, { wch: 25 }, { wch: 40 }];
    XLSX.utils.book_append_sheet(workbook, meetingSheet, 'Meeting Summary');

    // Sheet 4: Grades Overview
    const gradesHeaders = [
      'NIM',
      'Name',
      'Participation',
      'Project',
      'Quiz',
      'Assignment',
      'Midterm',
      'Final',
      'Numeric Grade',
      'Letter Grade',
      'Grade Index',
    ];
    const gradesData: (string | number)[][] = [gradesHeaders];

    data.students.forEach((student) => {
      const grade =
        data.findGradeForStudent(student) || ({
          aktivitas_partisipatif: null,
          hasil_proyek: null,
          quiz: null,
          tugas: null,
          uts: null,
          uas: null,
          nilai_angka: null,
          nilai_huruf: null,
          nilai_indeks: null,
        } as GradeRecord);

      const getValue = (key: keyof GradeRecord): string | number | '' => {
        const val = grade[key];
        if (val === null || val === undefined) return '';
        if (typeof val === 'object' && 'Float64' in val) return val.Valid ? val.Float64 : '';
        if (typeof val === 'object' && 'String' in val) return val.Valid ? val.String : '';
        if (typeof val === 'object' && 'Int32' in val) return val.Valid ? val.Int32 : '';
        return typeof val === 'number' || typeof val === 'string' ? val : '';
      };

      gradesData.push([
        student.nim,
        student.nama,
        (() => {
          const v = getValue('aktivitas_partisipatif');
          return v !== '' ? v : '-';
        })(),
        (() => {
          const v = getValue('hasil_proyek');
          return v !== '' ? v : '-';
        })(),
        (() => {
          const v = getValue('quiz');
          return v !== '' ? v : '-';
        })(),
        (() => {
          const v = getValue('tugas');
          return v !== '' ? v : '-';
        })(),
        (() => {
          const v = getValue('uts');
          return v !== '' ? v : '-';
        })(),
        (() => {
          const v = getValue('uas');
          return v !== '' ? v : '-';
        })(),
        (() => {
          const v = getValue('nilai_angka');
          return v !== '' ? v : '-';
        })(),
        (() => {
          const v = getValue('nilai_huruf');
          return v !== '' ? v : '-';
        })(),
        (() => {
          const v = getValue('nilai_indeks');
          return v !== '' ? v : '-';
        })(),
      ]);
    });

    // Add grade weights row at the bottom
    gradesData.push([
      'Component Weights',
      '',
      `${data.gradeWeights.aktivitas_partisipatif}%`,
      `${data.gradeWeights.hasil_proyek}%`,
      `${data.gradeWeights.quiz}%`,
      `${data.gradeWeights.tugas}%`,
      `${data.gradeWeights.uts}%`,
      `${data.gradeWeights.uas}%`,
      'Total: 100%',
      '',
      '',
    ]);

    const gradesSheet = XLSX.utils.aoa_to_sheet(gradesData);
    gradesSheet['!cols'] = [
      { wch: 15 }, // NIM
      { wch: 25 }, // Name
      { wch: 13 }, // Participation
      { wch: 10 }, // Project
      { wch: 8 }, // Quiz
      { wch: 12 }, // Assignment
      { wch: 10 }, // Midterm
      { wch: 8 }, // Final
      { wch: 13 }, // Numeric
      { wch: 12 }, // Letter
      { wch: 11 }, // Index
    ];
    XLSX.utils.book_append_sheet(workbook, gradesSheet, 'Grades Overview');

    return { workbook, XLSX };
  } catch (error) {
    throw new Error('Failed to generate XLSX. Please try again.');
  }
};

/**
 * Generate and download a PDF report
 */
export const downloadPDFReport = async (contentRef: HTMLDivElement, className: string, courseName: string) => {
  const pdf = await generatePDFReport(contentRef);
  const filename = `Class_Summary_${className}_${courseName.replace(/\s+/g, '_')}_${new Date().toISOString().split('T')[0]}.pdf`;
  pdf.save(filename);
};

/**
 * Generate and download an XLSX report
 */
export const downloadXLSXReport = async (data: ReportData) => {
  const { workbook, XLSX } = await generateXLSXReport(data);
  const filename = `Class_Summary_${data.className}_${data.courseName.replace(/\s+/g, '_')}_${new Date().toISOString().split('T')[0]}.xlsx`;
  XLSX.writeFile(workbook, filename);
};
