import { useState, useCallback, useMemo } from 'react';
import { useQueryClient } from '@tanstack/react-query';
import { useUpdateGrade, useCreateGrade } from '../../../hooks/useGrades';
import {
  extractNumeric,
  extractBobotValue,
  getStudentKrsId,
  calculateNumericGrade,
  calculateLetterGrade,
  generateGradeKey,
  getGradeComponents,
  extractPrimitive,
  GRADE_COMPARE_FIELDS,
  DEFAULT_GRADE_WEIGHTS,
  BOBOT_FIELDS,
} from '../utils';
import type { GradeRecord } from '../../../types';
import { apiService } from '../../../services/api';

type StudentLite = {
  nim?: string;
  krs_id?: string | number | null | undefined;
  id_krs?: string | number | null | undefined;
  krsId?: string | number | null | undefined;
  krs?: { id?: string | number | null | undefined; nim?: string; student?: { nim?: string } | null } | null;
  student?: { nim?: string } | null;
};

type ExtendedGradeRecord = GradeRecord & {
  student?: { nim?: string | null } | null;
  krs?: { student?: { nim?: string | null } | null; nim?: string | null } | null;
};

type ClassGradeData = Partial<{
  bobot_partisipatif: unknown;
  bobot_proyek: unknown;
  bobot_kuis: unknown;
  bobot_tugas: unknown;
  bobot_uts: unknown;
  bobot_uas: unknown;
}>;

interface UseGradeManagerProps {
  classIdNum: number;
  classId: string | undefined;
  students: StudentLite[];
  gradeRecords: GradeRecord[];
  classData: ClassGradeData | null | undefined;
}

export const useGradeManager = ({
  classIdNum,
  classId,
  students,
  gradeRecords,
  classData,
}: UseGradeManagerProps) => {
  const queryClient = useQueryClient();

  // State
  const [editedGrades, setEditedGrades] = useState<Record<string, Partial<GradeRecord>>>({});
  const [savingGradeIds, setSavingGradeIds] = useState<Set<number>>(new Set());
  const [completedGradeKeys, setCompletedGradeKeys] = useState<Set<string>>(new Set());

  // Bobot (grade weights) edit state
  const [editedBobots, setEditedBobots] = useState<{
    bobot_partisipatif?: number;
    bobot_proyek?: number;
    bobot_kuis?: number;
    bobot_tugas?: number;
    bobot_uts?: number;
    bobot_uas?: number;
  }>({});
  const [isSavingBobots, setIsSavingBobots] = useState(false);
  const [bobotError, setBobotError] = useState<string | null>(null);
  const [isBobotCompleted, setIsBobotCompleted] = useState(false);

  // Mutations
  const updateGradeMutation = useUpdateGrade();
  const createGradeMutation = useCreateGrade();

  // Grade weights calculation
  const gradeWeights = useMemo(() => {
    const partisipatif = editedBobots.bobot_partisipatif ?? extractBobotValue(classData?.bobot_partisipatif);
    const proyek = editedBobots.bobot_proyek ?? extractBobotValue(classData?.bobot_proyek);
    const kuis = editedBobots.bobot_kuis ?? extractBobotValue(classData?.bobot_kuis);
    const tugas = editedBobots.bobot_tugas ?? extractBobotValue(classData?.bobot_tugas);
    const uts = editedBobots.bobot_uts ?? extractBobotValue(classData?.bobot_uts);
    const uas = editedBobots.bobot_uas ?? extractBobotValue(classData?.bobot_uas);

    return {
      aktivitas_partisipatif: partisipatif ?? DEFAULT_GRADE_WEIGHTS.aktivitas_partisipatif,
      hasil_proyek: proyek ?? DEFAULT_GRADE_WEIGHTS.hasil_proyek,
      quiz: kuis ?? DEFAULT_GRADE_WEIGHTS.quiz,
      tugas: tugas ?? DEFAULT_GRADE_WEIGHTS.tugas,
      uts: uts ?? DEFAULT_GRADE_WEIGHTS.uts,
      uas: uas ?? DEFAULT_GRADE_WEIGHTS.uas,
    };
  }, [classData, editedBobots]);

  const gradeComponents = useMemo(() => {
    const effectiveClassData = {
      bobot_partisipatif: editedBobots.bobot_partisipatif ?? extractBobotValue(classData?.bobot_partisipatif),
      bobot_proyek: editedBobots.bobot_proyek ?? extractBobotValue(classData?.bobot_proyek),
      bobot_kuis: editedBobots.bobot_kuis ?? extractBobotValue(classData?.bobot_kuis),
      bobot_tugas: editedBobots.bobot_tugas ?? extractBobotValue(classData?.bobot_tugas),
      bobot_uts: editedBobots.bobot_uts ?? extractBobotValue(classData?.bobot_uts),
      bobot_uas: editedBobots.bobot_uas ?? extractBobotValue(classData?.bobot_uas),
    };
    return getGradeComponents(effectiveClassData);
  }, [classData, editedBobots]);

  const originalBobotWeights = useMemo(
    () => ({
      bobot_partisipatif: extractBobotValue(classData?.bobot_partisipatif) ?? DEFAULT_GRADE_WEIGHTS.aktivitas_partisipatif,
      bobot_proyek: extractBobotValue(classData?.bobot_proyek) ?? DEFAULT_GRADE_WEIGHTS.hasil_proyek,
      bobot_kuis: extractBobotValue(classData?.bobot_kuis) ?? DEFAULT_GRADE_WEIGHTS.quiz,
      bobot_tugas: extractBobotValue(classData?.bobot_tugas) ?? DEFAULT_GRADE_WEIGHTS.tugas,
      bobot_uts: extractBobotValue(classData?.bobot_uts) ?? DEFAULT_GRADE_WEIGHTS.uts,
      bobot_uas: extractBobotValue(classData?.bobot_uas) ?? DEFAULT_GRADE_WEIGHTS.uas,
    }),
    [classData]
  );

  const hasEditedBobots = useMemo(
    () =>
      BOBOT_FIELDS.some((field) => {
        const edited = editedBobots[field];
        if (edited === undefined) return false;
        return edited !== originalBobotWeights[field];
      }),
    [editedBobots, originalBobotWeights]
  );

  // Find the grade record for a student
  const findGradeForStudent = useCallback(
    (student: StudentLite): GradeRecord => {
      const extendedRecords = gradeRecords as ExtendedGradeRecord[];
      const studentNim = student?.nim ?? student?.krs?.nim ?? student?.student?.nim ?? '';
      let found: GradeRecord | ExtendedGradeRecord | undefined;
      found = extendedRecords.find(
        (g) => g.student?.nim === studentNim || g.krs?.student?.nim === studentNim || g.krs?.nim === studentNim
      );
      if (found) return found;

      const studentKrsId = getStudentKrsId(student);
      if (studentKrsId !== null) {
        found = gradeRecords.find((g) => {
          const k = extractNumeric(g.krs_id);
          return k !== null && k === studentKrsId;
        });
        if (found) return found;
      }

      found = extendedRecords.find((g) => Object.values((g as unknown) as Record<string, unknown>).some((v) => typeof v === 'string' && v === studentNim));
      if (found) return found;

      return {
        id: 0,
        krs_id: null,
        aktivitas_partisipatif: null,
        hasil_proyek: null,
        quiz: null,
        tugas: null,
        uts: null,
        uas: null,
        nilai_angka: null,
        nilai_huruf: null,
        nilai_indeks: null,
      } as GradeRecord;
    },
    [gradeRecords]
  );

  // Save a grade
  const saveGrade = useCallback(
    async (gradeKey: string, grade: GradeRecord, student?: StudentLite) => {
      const idNum = grade?.id && grade.id > 0 ? grade.id : null;
      if (idNum) setSavingGradeIds((prev) => new Set([...prev, idNum]));

      try {
        const editedGrade = editedGrades[gradeKey];
        if (!editedGrade) {
          return;
        }

        const gradeComponentFields: (keyof GradeRecord)[] = [
          'aktivitas_partisipatif',
          'hasil_proyek',
          'quiz',
          'tugas',
          'uts',
          'uas',
        ];

        const hasChanges = gradeComponentFields.some((field) => {
          const originalValue = extractNumeric(grade[field]);
          const editedValue = extractNumeric(editedGrade[field]);
          return originalValue !== editedValue;
        });

        if (!hasChanges) {
          setCompletedGradeKeys((prev) => new Set([...prev, gradeKey]));
          if (idNum) setSavingGradeIds((prev) => new Set([...prev].filter((id) => id !== idNum)));
          return;
        }

        const calculatedNumeric = calculateNumericGrade(editedGrade, gradeComponents);
        const nilaiAngka = calculatedNumeric !== null ? calculatedNumeric : extractNumeric(editedGrade.nilai_angka);

        let nilaiHuruf: string | undefined = undefined;
        let nilaiIndeks: number | undefined = undefined;

        if (nilaiAngka !== null) {
          const { huruf, indeks } = calculateLetterGrade(nilaiAngka);
          nilaiHuruf = huruf;
          nilaiIndeks = indeks;
        }

        const updateData = {
          aktivitas_partisipatif: extractNumeric(editedGrade.aktivitas_partisipatif) ?? undefined,
          hasil_proyek: extractNumeric(editedGrade.hasil_proyek) ?? undefined,
          quiz: extractNumeric(editedGrade.quiz) ?? undefined,
          tugas: extractNumeric(editedGrade.tugas) ?? undefined,
          uts: extractNumeric(editedGrade.uts) ?? undefined,
          uas: extractNumeric(editedGrade.uas) ?? undefined,
          nilai_angka: nilaiAngka ?? undefined,
          nilai_huruf: nilaiHuruf,
          nilai_indeks: nilaiIndeks,
        };

        if (grade.id && grade.id > 0) {
          await updateGradeMutation.mutateAsync({ id: grade.id, data: updateData });
        } else if (grade.krs_id) {
          const krsId = extractNumeric(grade.krs_id);
          await createGradeMutation.mutateAsync({ krs_id: Number(krsId), ...updateData });
        } else {
          const kFromStudent = getStudentKrsId(student);
          const krsId = kFromStudent ?? extractNumeric(editedGrade?.krs_id);
          if (krsId) {
            await createGradeMutation.mutateAsync({ krs_id: Number(krsId), ...updateData });
          } else {
            throw new Error('Missing KRS id for grade creation');
          }
        }

        setEditedGrades((prev) => {
          const next = { ...prev };
          delete next[gradeKey];
          return next;
        });
        setCompletedGradeKeys((prev) => new Set([...prev, gradeKey]));
      } catch (e) {
        throw e;
      } finally {
        if (idNum) {
          setSavingGradeIds((prev) => new Set([...prev].filter((id) => id !== idNum)));
        }
      }
    },
    [editedGrades, gradeComponents, updateGradeMutation, createGradeMutation]
  );

  // Update a grade field
  const updateGradeField = useCallback(
    (gradeKey: string, field: keyof GradeRecord, value: unknown) => {
      setEditedGrades((prev) => {
        const updatedGrade = {
          ...prev[gradeKey],
          [field]: value === '' ? null : value,
        };

        const isGradeComponent = gradeComponents.some((comp) => comp.key === field);

        if (isGradeComponent) {
          const calculatedNumeric = calculateNumericGrade(updatedGrade, gradeComponents);

          if (calculatedNumeric !== null) {
            const { huruf, indeks } = calculateLetterGrade(calculatedNumeric);

            updatedGrade.nilai_angka = calculatedNumeric;
            updatedGrade.nilai_huruf = huruf;
            updatedGrade.nilai_indeks = indeks;
          } else {
            updatedGrade.nilai_angka = null;
            updatedGrade.nilai_huruf = null;
            updatedGrade.nilai_indeks = null;
          }
        }

        return {
          ...prev,
          [gradeKey]: updatedGrade,
        };
      });
    },
    [gradeComponents]
  );

  // Bobot change handler
  const handleBobotChange = useCallback((field: string, value: string) => {
    const numValue = value === '' ? undefined : parseFloat(value);
    setEditedBobots((prev) => ({
      ...prev,
      [field]: numValue,
    }));
    setBobotError(null);
  }, []);

  // Cancel bobot changes
  const cancelBobots = useCallback(() => {
    setEditedBobots({});
    setBobotError(null);
    setIsBobotCompleted(true);
  }, []);

  // Enable bobot editing (reset completion state)
  const enableBobotEditing = useCallback(() => {
    setIsBobotCompleted(false);
  }, []);

  // Save bobot values
  const saveBobots = useCallback(async () => {
    try {
      setIsSavingBobots(true);
      setBobotError(null);

      const bobotData = {
        bobot_partisipatif: editedBobots.bobot_partisipatif ?? gradeWeights.aktivitas_partisipatif,
        bobot_proyek: editedBobots.bobot_proyek ?? gradeWeights.hasil_proyek,
        bobot_kuis: editedBobots.bobot_kuis ?? gradeWeights.quiz,
        bobot_tugas: editedBobots.bobot_tugas ?? gradeWeights.tugas,
        bobot_uts: editedBobots.bobot_uts ?? gradeWeights.uts,
        bobot_uas: editedBobots.bobot_uas ?? gradeWeights.uas,
      };

      const total =
        bobotData.bobot_partisipatif +
        bobotData.bobot_proyek +
        bobotData.bobot_kuis +
        bobotData.bobot_tugas +
        bobotData.bobot_uts +
        bobotData.bobot_uas;

      if (Math.abs(total - 100) > 0.01) {
        setBobotError(`Total must be 100%. Current total: ${total.toFixed(1)}%`);
        return false;
      }

      await apiService.updateClass(classIdNum, bobotData);

      await queryClient.invalidateQueries({ queryKey: ['class', classId] });

      setEditedBobots({});
      setIsBobotCompleted(true);
      return true;
    } catch (error) {
      setBobotError(error instanceof Error ? error.message : 'Failed to save grade weights');
      return false;
    } finally {
      setIsSavingBobots(false);
    }
  }, [editedBobots, gradeWeights, classIdNum, queryClient, classId]);

  // Enter edit mode
  const enterEditMode = useCallback(() => {
    const gradeMap: Record<string, Partial<GradeRecord>> = {};
    students.forEach((student) => {
      const grade = findGradeForStudent(student) || ({} as GradeRecord);
      const studentKrsId = getStudentKrsId(student);
      const safeNim = student?.nim ?? student?.krs?.nim ?? student?.student?.nim ?? '';
      const gradeKey = generateGradeKey(grade, studentKrsId, safeNim);
      gradeMap[gradeKey] = grade;
    });
    setEditedGrades(gradeMap);
  }, [students, findGradeForStudent]);

  // Exit edit mode
  const exitEditMode = useCallback(() => {
    setEditedGrades({});
    setCompletedGradeKeys(new Set());
    setEditedBobots({});
    setBobotError(null);
    setIsBobotCompleted(false);
  }, []);

  // Check if there are unsaved changes
  const hasUnsavedChanges = useMemo(() => {
    for (const [gradeKey, edited] of Object.entries(editedGrades)) {
      let original: GradeRecord | null = null;

      if (gradeKey.startsWith('id:')) {
        const id = Number(gradeKey.split(':')[1]);
        original = gradeRecords.find((g) => g.id === id) ?? null;
      } else if (gradeKey.startsWith('krs:')) {
        const krsId = Number(gradeKey.split(':')[1]);
        original =
          gradeRecords.find((g) => {
            const k = extractNumeric(g.krs_id);
            return k !== null && k === krsId;
          }) ?? null;
      } else if (gradeKey.startsWith('nim:')) {
        const nim = gradeKey.split(':')[1];
        const extendedRecords = gradeRecords as ExtendedGradeRecord[];
        original = extendedRecords.find((g) => g.student?.nim === nim || g.krs?.student?.nim === nim || g.krs?.nim === nim) ?? null;
      }

      if (!original) {
        if (Object.values(edited).some((v) => v !== null && v !== undefined && v !== '')) {
          return true;
        }
        continue;
      }

      for (const field of GRADE_COMPARE_FIELDS) {
        const eVal = extractPrimitive(edited[field as keyof GradeRecord]);
        const oVal = extractPrimitive((original as ExtendedGradeRecord)[field]);
        if (eVal !== oVal) return true;
      }
    }

    if (hasEditedBobots) return true;

    return false;
  }, [editedGrades, gradeRecords, hasEditedBobots]);

  return {
    // State
    editedGrades,
    savingGradeIds,
    completedGradeKeys,
    editedBobots,
    isSavingBobots,
    bobotError,
    isBobotCompleted,
    gradeWeights,
    gradeComponents,
    hasEditedBobots,
    hasUnsavedChanges,

    // State setters
    setEditedGrades,
    setCompletedGradeKeys,

    // Methods
    findGradeForStudent,
    saveGrade,
    updateGradeField,
    handleBobotChange,
    cancelBobots,
    enableBobotEditing,
    saveBobots,
    enterEditMode,
    exitEditMode,
  };
};
